.catalog {
    @include tablet {
        padding: 0 25px;
    }

    .fp-tableCell {
        display: flex;
        justify-content: flex-end;

    }

    &__title-wrap {
        padding-top: 169px;
        width: 30%;
        text-align: center;

        @include tablet {
            width: 100%;
            padding-top: 60px;
            text-align: left;
        }
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, minmax(30%, 440px));

        @include tablet {
            margin-top: 30px;
        }

        @include mobile {
            display: none;
        }
    }

    &__grid-item {
        border-left: 1px solid #56575C;
        border-bottom: 1px solid #56575C;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        padding: 23px;
        cursor: pointer;
        transition: all .3s linear;

        @include tablet {
            max-width: 100%;
        }

        @include mobile {
            border: 0;
        }

        &:nth-child(n+4) {
            border-bottom: 0;
            justify-content: flex-start;
        }

        &:nth-child(1),
        &:nth-child(4) {
            border-left: 0;
        }

        &:hover {
            background-color: $grey_light;
            color: $dark;

            .catalog__grid-item-img svg:nth-child(2) {

                path,
                rect,
                circle {
                    stroke-dashoffset: 0;
                    transition: all ease-in-out 2.5s;
                }
            }
        }
    }

    &__number {
        color: $gold;
        font-size: 15px;
        line-height: 16px;
        align-self: start;
    }

    &__grid-item-img {
        margin-top: 72px;
        position: relative;
        height: 20vh;
        display: flex;
        align-items: center;
        max-width: 100%;

        @include tablet {
            max-width: 90%;
        }

        @media screen and (min-width:$desktop-width) and (max-height: 800px) {
            margin-top: 5vh;
        }

        svg,
        img {
            max-height: 100%;
            max-width: 100%;
        }

        path,
        rect,
        circle {
            stroke-width: 2px;
        }

        svg:nth-child(2),
        img:nth-child(2) {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);

            path,
            rect,
            circle {
                stroke: $blue;
                stroke-dasharray: 1500;
                stroke-dashoffset: 1500;
                transition: all ease-in-out 1.5s;
            }
        }

        &_medium {

            svg:nth-child(2),
            img:nth-child(2) {

                path,
                rect,
                circle {
                    stroke-dasharray: 700;
                    stroke-dashoffset: 700;
                }
            }
        }

        &_small {

            svg:nth-child(2),
            img:nth-child(2) {

                path,
                rect,
                circle {
                    stroke-dasharray: 250;
                    stroke-dashoffset: 250;
                }
            }

        }
    }

    &__grid-item-title {
        margin-top: 30px;
        font-size: 20px;
        line-height: 23px;
        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;
    }

    &__slider {
        display: none;
        margin-top: 30px;

        &.swiper-container {
            padding-bottom: 20px;
        }

        @include mobile {
            display: block;
        }
    }

    &__scrollbar {
        margin-top: 20px;

        .swiper-scrollbar-drag {
            background: $gold;
        }
    }
}